import React, { Component } from 'react';

import { Link } from 'react-router-dom';

import { geUserByID } from '../../Api'

import "./FeedItem.css"

class FeedItem extends Component{
    constructor(){
        super();
        this.state = {
            nome: "",
            idAutor: "",
        }
    }

    componentDidMount(){
        this.callAutorId()
    }

    async callAutorId() {
        const autor = await geUserByID(this.props.autorId)

        this.setState({
            nome: autor.nome,
            idAutor: autor.id
        })
    }

    render(){
        return(
            <div className="FeedItem" >
            <Link className="TrendingLinkInfo" to={"/agora/usuario/" + this.state.idAutor}>
                <div className="personalInfo">
                    <img className="circularPhoto" src="data:image/png;base64," alt="Avatar" />
                    <div className="authorName">
                        {this.state.nome}
                    </div>
                </div>
            </Link>
    
            <Link className="TrendingLink" to={"/agora/artigo/" + this.props.id}>
                <div className="textInfo">
                    <div className="title">
                    {this.props.titulo}
                    </div>
                    <div className="description">
                    {this.props.descricao}
                    </div>
                </div>
            </Link>
        </div >
        )
    }
}



export default FeedItem;