import React, { Component } from "react";
import { connect } from 'react-redux';


import { getArticleByID, getisLiked, like, unlike } from "../../Api"

import "./Article.css"

//to do: Fazer tela de loading

class Article extends Component {
    constructor() {
        super();
        this.state = {
            Titulo: "",
            Descricao: "",
            Conteudo: "",
            AutorNome: "",
            userId: "",
            artigoId: "",
            isLiked: false
        }
    }

    componentDidMount() {
        this.callArticleApi()
    }


    async callArticleApi() {
        const artigo = await getArticleByID(this.props.id)
        if (this.props.currentUser) {
            const isLiked = await getisLiked(this.props.currentUser.id, this.props.id);
            this.setState({ isLiked: isLiked });
            this.setState({ userId: this.props.currentUser.id, artigoId: this.props.id })
        }
        this.setState({ ...artigo });
        
    }

    async handleLike() {
        const givelike = await like(this.state.userId, this.state.artigoId);
        this.setState({isLiked: true});
    }


    async handleDislike() {
        const givedislike = await unlike(this.state.userId, this.state.artigoId);
        this.setState({isLiked: false});

    }

    render() {
        return (
            <div className="artigo">
                <div className="artigoInformacao">
                    <img className="circularFotoArtigo" src="data:image/png;base64," alt="Avatar" />
                    <span className="artigoAutor">{this.state.AutorNome}</span>
                    {this.props.currentUser ?
                        <button className="artigoBotao">Seguir</button>
                        : null
                    }
                </div>
                <div className="artigoConteudo">
                    <h1>{this.state.Titulo}</h1>
                    <span>{this.state.Descricao}</span>
                    <p className="articleTextContent">{this.state.Conteudo}</p>
                    <div className="curtidas">
                        {this.props.currentUser ?
                            this.state.isLiked ?
                                <button className="descurtirBotao" onClick={this.handleDislike.bind(this)}>Curtir</button>
                                : <button className="curtirBotao" onClick={this.handleLike.bind(this)}>Curtir</button>
                            : null
                        }
                        <span className="curtidasQnt">{this.state.Qnt_likes} Curtidas</span>
                    </div>
                </div>
            </div>
        )
    }
}



const mapStateToProps = (state) => ({
    currentUser: state.user.currentUser,
})

export default connect(
    mapStateToProps
)(Article);
