import React, { Component } from 'react'
import UserArtigos from '../../components/userartigos/UserArtigos'

import { geUserByID } from '../../Api'

import './Perfil.css';

class Perfil extends Component {
    constructor() {
        super();
        this.state = {
            id: "",
            nome: "",
        }
    }

    componentDidMount() {
        this.callAutorId()
    }
    
    async callAutorId() {
        const autor = await geUserByID(this.props.match.params.id)

        this.setState({
            nome: autor.nome,
            id: autor.id
        })
    }

    render() {
        console.log(this.state);

        return (
            <div className="perfil">
                <div className="perfilInfo">
                    <h1 className="perfilName">{this.state.nome}</h1>
                    <img className="circularFotoArtigo" src="data:image/png;base64," alt="PerfilImage" />
                </div>
                {
                    this.state.id ? <UserArtigos id={this.state.id} /> : null
                }


            </div>
        )
    }
}

export default Perfil;